
* {
  padding: 0;
  margin: 0;
}

html, body {
  font-family: Arial, sans-serif;
  scroll-behavior: smooth;
  box-sizing:content-box;
  width: 100vw;
  overflow-x: hidden;
}


.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: black;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out;
  z-index: 998;
}

.overlay.show {
  opacity: 1;
  visibility: visible;
  overflow: hidden;
}

.overlay a {
  display: block;
  position: relative;
  color: white;
  text-decoration: none;
  left: 40%;
  padding: 22px 0px;
  top: 20%;
  justify-content: center;
}

.overlay .link2 {
  position: relative;
  left: 132px;
}

.overlay .link3 {
  position: relative;
  left: 127px;
}

.container1 {
  width: 100%;
  height: 158px;
  left: 0px;
  top: 0px;
  background: linear-gradient(94.23deg, #24CE9E 47.9%, rgba(36, 206, 158, 0) 148.7%);
  border-radius: 0px 0px 20px 20px;
  z-index: -1;
}

.burger {
  position: relative;
  left: 286px;
  bottom: 55px;
  width: 32px;
  height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  transition: transform 0.5s;
  -webkit-tap-highlight-color: transparent;
  z-index: 1000;
}

.mydarkswitch {
  position: relative;
  bottom: 536px;
  right: 19px;
}

.myswitch {
  border: 1px solid black;
  outline: none;
  

}
.line {
  height: 2px;
  background-color: rgb(255, 255, 255);
  transition: background-color 0.5s, transform 0.5s;
}


.cross .line1 {
  transform: rotate(45deg) translate(5px, 5px);
  background-color: white;
}

.cross .line2 {
  opacity: 0;
}

.cross .line3 {
  transform: rotate(-45deg) translate(8px, -8px);
  background-color: white;
}


.container1 .text {
  position: absolute;
  width: 105px;
  height: 24px;
  left: 23px;
  top: 64px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
}

.container1 p {
  position: absolute;
  width: 183px;
  height: 15px;
  left: .2px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: #FFFFFF;
}


.search {
  position: absolute;
  width: 280px;
  height: 45px;
  left: 35px;
  top: 127px;
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
}

.search input {
  position: relative;
  width: 220px;
  height: 45px;
  border: none;
  outline: none;
}

::placeholder {
  position: relative;
  left: 12px;
}

.search [type='text'] {
  padding-left: 33px;
}

.text1 p {
  position: relative;
  top: 42px;
  left: 22px;
  color: black;
  font-size: large;
  font-weight: 800;
}

.container2 {
  width: 1vw;
  position: relative;
  top: 83px;
  left: 32px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 32px;
}

.container2 img {
  width: 133px;
  border-radius: 22px;
}

.container2 .box5 {
  position: relative;
  left: 33vw;
  grid-column-end: 2;
}

.container2 button {
  border: none;
  background-color: transparent;
  text-decoration: underline;
}

.discount {
  display: flex;
  align-items: center;
  position: relative;
  width: 380px;
  height: 78px;
  left: 0px;
  top: 31px;
  color: white;
  font-weight: 700;
  background: #24CE9E;
}

.discount p {
  position: relative;
  left: 17px;
  top: 9px;
  width: 166px;
}

.discount button {
  position: relative;
  width: 84px;
  height: 31px;
  left: 84px;
  background: #FFFFFF;
  border-radius: 15px;
  font-size: 12px;
}

.text2 p {
  position: relative;
  color: black;
  top: 92px;
  left: 22px;
  font-weight: 800;
}

.container3 {
  width: 1vw;
  position: relative;
  top: 143px;
  left: 25px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 32px;
  row-gap: 66px;
}

.container3 img {
  width: 133px;
  border-radius: 12px;
  box-shadow: 6px 2px 11px black;
}

.content {
  opacity: 0;
  transform: translateY(80px); /* For slide-up effect */
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

.content.visible {
  opacity: 1;
  transform: translateY(0);
}

.text3 {
  position: relative;
  top: 42px;
  left: 22px;
  color: black;
  font-size: large;
  font-weight: 800;
}

